#image-form{

    #file-input{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border: none;
        background-color: rgb(255, 255, 255);
        opacity: 0;
    }

    input:last-of-type{
        margin-left: -50px;
    }

    div{
        position: absolute;
        background-color: rgba(65, 65, 65, 0.5);
        width: 100%;
        height: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        text-align: center;
        display: table;

        label{
            color: white;
            text-transform: uppercase;
            font-weight: bolder;
            font-size: 12px;
            display: table-cell;
            vertical-align: middle;
            cursor: pointer;
        }
    }

    

    #profil-image-preview{
        width: 150px;
        height: 150px;
        background-color: #f1f1f1;
        // background-size: cover;
        // background-position: center;
        display: none;
    }
}