.messages-page{
    text-align: left;
    background-color: #f1f1f1;
    min-height: calc(100vh - 55px);
    width: 100%;
    display: flex;

    .messages-left{
        width: 20%;
        height: fit-content;
        max-width: 300px;       
    }

    .messages-main{
        background-color: $bg3;
        width: calc(100% - 300px);
        position: relative;
        // padding: 0 50px;

        .msg-contact-infos{
            // background-color: #ccc;
            height: 60px;
            display: flex;
            align-items: center;
            padding-left: 20px;
            font-weight: 600;
            color: $c1;
            border-bottom: solid 1px $bg4;

            // .contact-img{
            //     height: 40px;
            //     width: 40px;
            //     background-color: #fff;
            //     background-size: cover;
            //     background-position: center;
            //     border-radius: 50%;
            //     margin-right: 25px;
            //     border: solid 1px #ddd;
            // }

            hr{
                height: 20px;
                width: .5px;
                background-color: $bg4;
                border: none;
                margin: 0;
                margin-left: 30px;
                margin-right: 30px;
            }

            .infos-bio{
                font-size: 13px;
            }
        }

        .messagesWrapper{
            list-style-type: none;
            padding-left: 0;
            display: flex;
            flex-direction: column;
            overflow-y: scroll;
            height: calc(100vh - 190px);
            padding-top: 10px;
            margin-top: 0;
            margin-bottom: 0;

            &::-webkit-scrollbar{
                width: 10px;
            }
            &::-webkit-scrollbar-track {
                background: $bg2; /* color of the tracking area */
                border-radius: 20px;
                margin-top: 5px;
            }
            &::-webkit-scrollbar-thumb {
                background-color: $bg1; /* color of the scroll thumb */
                border-radius: 20px; /* roundness of the scroll thumb */
                
            }


            li{
                padding-left: 20px;
                color: $c2;
                padding-top: 2px;
                padding-bottom: 2px;
                font-size: 15px;
                display: flex;
                margin-bottom: 5px;

                .msg-pp{
                    width: 35px;
                    height: 35px;
                    background-color: #fff;
                    background-size: cover;
                    background-position: center;
                    border-radius: 50%;
                    margin-right: 10px;
                    border: solid .5px $c1;
                }

                .msg-name{
                    font-weight: 600;
                    color: $c2;
                    font-size: 15px;
                }

                .msg-content{
                    color: $c1;
                    font-size: 14px;
                }

                .msg-container{
                    height: auto;
                    padding: 0;
                    margin: 0;
                }

                p{
                    margin: 0;
                    padding: 0;
                }

                &:hover{
                    background-color: $bg4;
                }
            }
        }

        .input-msg{
            box-sizing: border-box;
            background-color: $bg4;
            position: absolute;
            bottom: 20px;
            left: 20px;
            width: calc(100% - 40px);
            padding: 10px;
            color: $c2;
            border: none;
            font-size: 15px;
            border-radius: 5px;

            &:focus{
                outline: none;
            }

            &::placeholder{
                color: $c1;
            }
        }
    }

    .messages-right{
        background-color: $bg3;
        padding: 10px;
        border-left: solid 1px $bg4;
        width: 20%;
        max-width: 300px;
        margin-left: auto;

        ul{
            list-style-type: none;
            padding: 0;
            margin: 0;
    
            li{
                display: flex;
                align-items: center;
                border-radius: 10px;
                padding: 8px;
    
                &:hover{
                    cursor: pointer;
                    background-color: $bg4;
                }
    
                // a{
                //     padding: 8px;
                //     text-decoration: none;
                //     width: 100%;
                //     display: flex;
                //     align-items: center;
    
                    .users-img{
                        height: 40px;
                        width: 40px;
                        background-color: #fff;
                        background-size: cover;
                        background-position: center;
                        border-radius: 50%;
                        margin-right: 15px;
                        border: solid 1px #ddd;
                    }
    
                    .messages-infos{
                        text-align: left;
                        
                        .users-name{
                            color: $c1;
                            font-weight: 600;
                            margin: 0;
                            padding: 0;
                        }

                        .last-msg{
                            margin: 0;
                            padding: 0;
                            color: $c2;
                            font-size: 14px;
                            white-space: nowrap;
                            overflow: hidden;
                            max-width: 200px;
                        }
                    }
                    
                // }
            }
        }
    }

}
