.contacts-page{
    text-align: left;
    min-height: calc(100vh - 55px);
    width: 100%;
    display: flex;

    .contacts-left{
        width: 20%;
        height: fit-content;
        max-width: 300px;       
    }

    .contacts-main{
        background-color: $bg3;
        width: calc(80% - 300px);
        padding: 0 50px;
    }

    .contacts-right{
        background-color: $bg2;
        padding-left: 10px;
        border-left: solid 1px $bg2;
    }

    .contacts-main, .contacts-right{
    
        h3{
            font-size: 18px;
            color: $c1;
            text-align: left;
            margin: 0;
            margin-bottom: 15px;
            margin-top: 20px;
        }

        hr{
            border: none;
            background: #ccc;
            height: 1px;
        }

        ul{
            list-style-type: none;
            padding: 0;
            margin: 0;
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 50px;
            // background-color: #555;

            li{
                width: 300px;
                height: 100px;
                background-color: $bg2;
                border-radius: 10px;
                overflow: hidden;
                position: relative;
                margin-right: 15px;
                margin-bottom: 15px;
                border: solid 1px #ddd;
                box-shadow: 1px 1px rgba(219, 219, 219, 0.479);
                display: flex;

                a{
                    text-decoration: none;
                }

                .users-img{
                    background-color: #fff;
                    background-size: cover;
                    background-position: center;
                    width: 100px;
                    height: 100%;
                    margin-right: 5px;
                }

                .users-name{
                    padding: 0;
                    margin: 0;
                    margin-top: 5px;
                    font-size: 16px;
                    font-weight: bold;
                    color: $c2;
                }

                .common-contacts{
                    padding: 0;
                    margin: 0;
                    font-size: 14px;
                    color: $c2;
                }

                .btn{
                    cursor: pointer;
                    text-transform: uppercase;
                    background-color: #e5e5e5;
                    font-size: 12px;
                    font-family: 'Open Sans', sans-serif;
                    font-weight: bold;
                    padding: 7px 12px;
                    border-radius: 5px;
                    color: #555;
                    border: none;
                    margin-left: auto;
                    z-index: 10;
                    margin-right: 8px;
                    width: 90px;
                    position: absolute;
                    bottom: 7px;
                    right: 95px;
    
                    &:hover{
                        background-color: #999;
                    }

                    &:last-of-type{
                        right: 0px;
                    }
                }

            }
        }
    }
}


