.profil-page{
    padding: 0;
    min-height: calc(100vh - 55px); // prend en compte la hauteur du header
    background-color: $bg2;
    display: flex;
    width: 100%;

    .profil-left{
        width: 20%;
        height: fit-content;
        max-width: 300px;
    }

    .profil-main{
        background-color: $bg3;
        width: 100%;
        padding-top: 50px;


        .profil-container{
            max-width: 1200px;
            width: 80%;
            margin: 0 auto;
            background-color: #fff;
            border-top-left-radius: 20px;
            border-top-right-radius: 20px;
            overflow: hidden;
    
            .profil-cover{
                height: 150px;
                width: 100%;
                background-size: cover;
                background-position: center;
            }
    
            .profil-raw{
                margin-top: -50px;
                height: 150px;
                display: flex;
                padding: 20px 20px;
                border-bottom: solid 1px #ccc;
                
                .profil-picture{
                    position: relative;
                    border-radius: 50%;
                    width: 150px;
                    height: 150px;
                    margin-right: 5px;
                    border: solid 5px white;
                    background-color: #f1f1f1;
                    background-size: cover;
                    background-position: center;
                    // overflow: hidden;
    
                    img{
                        width: 100%;
                    }
                }
    
                .profil-data{
                    margin-left: 15px;
                    margin-top: 50px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    margin-right: auto;
    
                    .profil-pseudo, .profil-bio{
                        margin: 0;
                        padding: 0;
                        font-family: 'Open Sans', sans-serif;
                    }
    
                    .profil-pseudo{
                        font-weight: 600;
                        font-size: 20px;
                    }
    
                    .profil-bio{
                        font-size: 15px;
                    }
                }
    
                .profil-update-btn{
                    // text-decoration: none;
                    cursor: pointer;
                    border: none;
                    background-color: #e5e5e5;
                    color: #555;
                    font-weight: 600;
                    font-size: 15px;
                    height: fit-content;
                    padding: 10px;
                    padding-left: 35px;
                    position: relative;
                    border-radius: 5px;
                    margin-top: calc(150px/2 + 50px);
                    transform: translateY(-50%);
    
                    &::before{
                        content: "";
                        display: block;
                        background: url("./../../../public/img/icons/edit.svg") no-repeat;
                        width: 45px;
                        height: 45px;
                        transform: scale(0.5);
                        position: absolute;
                        left: -5px;
                        top: -4px;
                    }
                    
                    &:hover{
                        background-color: #ddd;
                    }
                }
    
            } 
        } 
    }

      
}

