.navbar{
    // background: white;
    background-color: $bg1;
    position: relative;
    height: 55px;
    display: flex;
    // justify-content: flex-end;
    align-items: center;

    &:after {
        content: '';
        position: absolute;
        left: 0;
        // bottom: -4px;
        // height: 4px;
        width: 100%;
        bottom: 0px;
        height: 1px;
        // background: linear-gradient(to bottom, rgb(160, 160, 160) 0%, rgb(228, 228, 228) 50%, transparent 75%);
        background: #222;
    }

    .logo{
        color: $c2;
        text-decoration: none;
        margin-left: 20px;
        margin-right: auto;
    }

    .nav-profil{
        cursor: pointer;
        padding: 0px;
        padding-right: 10px;
        padding-left: 5px;
        height: 40px;
        width: auto;
        display: flex;
        border-radius: 40px;
        background-color: $bg3;
        justify-content: space-between;
        align-items: center;
        text-decoration: none;

        &:hover{
            background-color: $bg4;
        }

        .nav-picture{
            border-radius: 50%;
            width: 32px;
            height: 32px;
            margin-right: 5px;
            background-color: #fff;
            background-size: cover;
            background-position: center;
        }
    
    }

    .nav-pseudo{
        font-size: 15px;
        color: $c2;
        font-weight: 500;
        height: min-content;
        padding: 0;
        margin: 0;
    }

    

    .logout-icon, .login-icon {
        cursor: pointer;
        transform: scale(0.6);
        list-style: none;
        margin-top: 3px;
        margin-left: 0px;
        margin-right: 15px;
        background-color: $bg3;
        border-radius: 50%;
        height: 40px;
        padding-top: 9px;
        padding-bottom: 11px;
        padding-left: 12px;
        padding-right: 8px;

        &:hover{
            // transform: scale(0.55);
            background-color: $bg4;
        }
    }

    
}

