.form-container{
    font-family: 'Open Sans', sans-serif;
    background-color: $bg4;
    width: fit-content;
    padding: 40px;
    border-radius: 5px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: left;
    // border: solid 1px #ddd;

    #sign-in-form, #sign-up-form{

        h2{
            text-align: center;
            color: $c2;
            font-weight: 600;
            font-size: 25px;
            padding: 0;
            margin: 0;
        }

        h3{
            margin: 0;
            text-align: center;
            padding: 0;
            margin-bottom: 30px;
            color: $c1;
            font-weight: 500;
            font-size: 15px;
        }

        .sign-in-errors, .sign-up-errors{
            color: red;
            margin-bottom: 20px;
        }

        label{
            font-size: 11px;
            font-weight: 600;
            color: $c1;
            text-transform: uppercase;
        }
        
        input[type=text], input[type=password]{
            font-family: 'Open Sans', sans-serif;
            font-size: 15px;
            height: 40px;
            width: 400px;
            border-radius: 5px;
            border: solid 1px grey;
            margin-top: 5px;
            margin-bottom: 20px;
            padding: 0 15px;
            box-sizing: border-box;
        }

        input[type=submit]{
            font-family: 'Open Sans', sans-serif;
            font-size: 11px;
            height: 40px;
            width: 400px;
            border-radius: 5px;
            border: solid 1px #333;
            padding: 0 15px;
            margin-top: 25px;
            background-color: #333;
            text-transform: uppercase;
            font-weight: bold;
            color: white;
            margin-top: 15px;
            letter-spacing: 1px;
            cursor: pointer;

            &:hover{
                background-color: $bg1;
            }
        }
    }

    p{
        display: none;
        margin: 0;
        padding: 0;
        margin-top: 10px;
        font-size: 13px;
        color: $c1;
        width: fit-content;
        &:hover{
            cursor: pointer;
        }

        span{
            color: $c2;
            padding: 0;
            margin: 0;
            margin-left: 5px;
            z-index: -1;
        }
        
    }.active-btn{
        display: flex;
    }
    
}


