.home-page{
    text-align: center;
    background-color: $bg2;
    min-height: calc(100vh - 55px);
    width: 100%;
    display: flex;

    .home-left{
        width: 20%;
        height: fit-content;
        max-width: 300px;  
    }

    .home-main{
        background-color: $bg3;
        width: 80%;
        padding-top: 50px;

        h1{
            color: $c1;
            padding: 0;
            margin: 0;
        }
    }

    .home-side{
        height: fit-content;
        width: 20%;
        max-width: 350px;
        margin-left: auto;

        hr{
            width: calc(100% - 16px - 30px);
            height: 1px;
            border: none;
            background-color: #ccc;
        }
    }
    
}