.list{
    // background-color: #fff;
    padding: 15px;
    margin: 0;

    h3{
        font-size: 17px;
        color: $c1;
        text-align: left;
        margin: 0;
        margin-bottom: 15px;
        margin-left: 8px;
    }

    ul{
        list-style-type: none;
        padding: 0;
        margin: 0;

        li{
            display: flex;
            align-items: center;
            border-radius: 10px;

            &:hover{
                cursor: pointer;
                background-color: $bg4;
            }

            a{
                padding: 8px;
                text-decoration: none;
                width: 80%;
                display: flex;
                align-items: center;

                .users-img{
                    height: 40px;
                    width: 40px;
                    background-color: #fff;
                    background-size: cover;
                    background-position: center;
                    border-radius: 50%;
                    margin-right: 15px;
                    border: solid 1px #ddd;
                }

                .users-infos{
                    text-align: left;
                    
                    .users-name{
                        color: $c2;
                        font-weight: 500;
                        margin: 0;
                    }

                    .common-contacts{
                        color: $c2;
                        font-size: 13px;
                        padding: 0;
                        margin: 0;
                    }
                }

                
    
                
            }

            .btn{
                cursor: pointer;
                background-color: $bg1;
                font-size: 12px;
                font-family: 'Open Sans', sans-serif;
                font-weight: bold;
                padding: 7px 12px;
                border-radius: 5px;
                color: $c2;
                border: none;
                margin-left: auto;
                z-index: 10;
                margin-right: 8px;
                width: 90px;

                &:hover{
                    background-color: $bg2;
                }
            }
        }
    }
}