.navmenu{
    padding: 15px;
    margin: 0;
    background-color: $bg2;
    height: calc(100vh - 85px);

    ul{
        list-style-type: none;
        padding: 0;
        margin: 0;

        li{
            display: flex;
            align-items: center;
            border-radius: 10px;

            &:hover{
                cursor: pointer;
                background-color: $bg4;
            }

            a{
                padding: 8px;
                text-decoration: none;
                width: 100%;
                display: flex;
                align-items: center;

                .user-img{
                    height: 35px;
                    width: 35px;
                    background-color: #fff;
                    background-size: cover;
                    background-position: center;
                    border-radius: 50%;
                    margin-right: 15px;
                    border: solid 1px #ddd;
                }
    
                .navmenu-name{
                    color: $c2;
                    font-weight: 600;
                    margin: 0;
                    font-size: 15px;
                }

                .navmenu-img{
                    transform: scale(1);
                    margin-right: 15px;
                }
            }
        }
    }
}